.promotion {
	position: relative;
	&__bg {
		position: absolute;
		&:nth-child(1) {
			left: 0;
			top: 750px;
		}
	}
}

.promotion-block {
	width: 100%;
	position: relative;
	padding: 270px 0 0 0;
	&__containers {
		max-width: 1440px;
		width: 100%;
		margin: 0 auto;
		padding: 0 60px;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: flex-start;
	}
	&__title {
		margin: 35px 0 45px 0;
		font-family: Prata;
		font-style: normal;
		font-weight: normal;
		font-size: 56px;
		line-height: 71px;
		color: #021245;
	}
	&__nav {
		background: #ffffff;
		border: 2px solid #003f80;
		border-radius: 30.5px;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		padding: 8px;
		margin: 0 0 70px 0;
		&-items {
			background: #fff;
			cursor: pointer;
			border-radius: 100px;
			padding: 10px 30px;
			font-family: TT Norms;
			font-style: normal;
			font-weight: normal;
			font-size: 20px;
			line-height: 24px;
			text-align: center;
			letter-spacing: 0.09em;
			color: #003f80;
		}
		&-active {
			background: #003f80;
			color: #ffffff;
		}
	}
	&__list {
		width: 100%;
		display: grid;
		grid-template-columns: repeat(auto-fill, minmax(415px, 1fr));
		gap: 35px 23px;
	}
	&__items {
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: flex-start;
		background: #ffffff;
		box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
		position: relative;
	}
	&__photo {
		height: 375px;
		width: 100%;
		overflow: hidden;
		& img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	&__time {
		padding: 8px 10px;
		background: #003f80;
		border-radius: 27px;
		display: flex;
		align-items: center;
		justify-content: center;
		position: absolute;
		bottom: 20px;
		right: 20px;
		& p {
			font-family: PT Serif;
			font-style: normal;
			font-weight: normal;
			font-size: 14px;
			line-height: 19px;
			color: #ffffff;
		}
	}
	&__times {
		background: #ffffff;
		border-radius: 27px;
		padding: 4px 13px;
		display: flex;
		align-items: center;
		justify-content: center;
		margin: 0 0 0 6px;
		& span {
			font-family: PT Serif;
			font-style: normal;
			font-weight: normal;
			font-size: 16px;
			line-height: 21px;
			color: #003f80;
			margin: 0 0 0 6px;
		}
	}
	&__date {
		padding: 8px 10px;
		background: #003f80;
		border-radius: 27px;
		display: flex;
		align-items: center;
		justify-content: center;
		position: absolute;
		bottom: 20px;
		right: 20px;
		& p {
			font-family: PT Serif;
			font-style: normal;
			font-weight: normal;
			font-size: 14px;
			line-height: 19px;
			color: #ffffff;
		}
	}
	&__dates {
		background: #ffffff;
		border-radius: 27px;
		padding: 4px 13px;
		display: flex;
		align-items: center;
		justify-content: center;
		margin: 0 0 0 6px;
		& span {
			font-family: PT Serif;
			font-style: normal;
			font-weight: normal;
			font-size: 16px;
			line-height: 21px;
			color: #003f80;
		}
	}
	&__titles {
		padding: 20px 16px;
		background: #ffffff;
		width: 100%;
		& p {
			font-family: TT Norms;
			font-style: normal;
			font-weight: normal;
			font-size: 24px;
			line-height: 20px;
			display: flex;
			align-items: center;
			color: #003f80;
		}
	}
}
